import {instance as axios} from '../jslib/HttpRequest'

// 
export function listData(obj) {
  return new Promise((resolve, reject) => {
    axios({
      url: 'employee/getAllEmployeeInMobile',
      method: 'post',
      data: obj
    })
      .then((response) => {
        resolve(response)
      })
      .catch((error) => {
        console.log(error)
      })
  })
}

