import MescrollVue from 'mescroll.js/mescroll.vue'
import "mescroll.js/mescroll.min.css";
import { Indicator } from "mint-ui";
import { selectCart } from "../../server/employee";
import { log } from 'util';
export default {
  name: "client",
  components:{MescrollVue },
  data() {
    return {
      imgUrl: require("@/assets/img/seach.png"),
      value: "",
      count: 0,
      mescroll:null,
      mescrollUp: { // .
          callback: this.getList,
          htmlNodata: '<p class="upwarp-nodata">  </p>',
          noMoreSize: 5, 
          page: {
            num: 0,
            size: 50,
            total: 0
          },
          empty: {
          warpId: "main", 
          icon: "./static/mescroll/mescroll-empty.png", 
          tip: "~" //
          },
        htmlLoading:'<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">..</p>',
      },
      pages: {
        page: 1,
        size: 50,
        total: 0
      },
      dataList: [] // 
    };
  },
  methods: {
    mescrollInit (mescroll) {
      this.mescroll = mescroll  
    },
    getList(page, mescroll) {
    const that = this
      selectCart({
        page: page.num,
        limit: page.size,
        customerName:this.value
      }).then(e => {
        // console.log(page.num);
        if (e.data.code == 200) {
          var arr = e.data.data
           let data = page.num == 1 ? [] : this.dataList;
           this.dataList = this.dataList.concat(arr)
          //  console.log(this.dataList);
           this.$nextTick(() => {
              this.mescroll.endSuccess(arr.length)
            })
        }
      });
    },
     handleSeach(){
       let searchList = []
       const that = this
      selectCart({
        page: this.pages.page,
        limit: this.pages.size,
        customerName:this.value
      }).then(e => {
        // console.log(page.num);
        if (e.data.code == 200) {
          this.searchList=e.data.data
          this.dataList=this.searchList
        }
      });
    },
    handleLink(companyNo,companyName){
      console.log(111);
      localStorage.setItem("companyNo", JSON.stringify(companyNo))
      localStorage.setItem("companyName", JSON.stringify(companyName))
      this.$router.push({
        path:'/AddEmployee',
      })
    }
  }
};



